import React, { useState } from 'react'
import SimpleModal from '../../common/modal/SimpleModal'
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField'
import Paper from '@material-ui/core/Paper'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'
import Box from '@material-ui/core/Box'
import { makeStyles } from '@material-ui/core/styles'
import Button from '@material-ui/core/Button'
import { connect } from 'react-redux'
import {
    UI_LOGIN_MODAL_CLOSE, 
    UI_LOGIN_SET_ACT_TAB
} from '../../../redux/actionTypes'

const useStyles = makeStyles(theme => ({
    menuButton: {
      marginRight: theme.spacing(2),
    },
    loginModal: {
        maxWidth: 600,
        padding: 0
    }
}))

const LoginModal = props => {
    const classes = useStyles()
    const {isOpen, handleClose, visibleTabIndex, title, changeTab} = props
    const handleTabSwitch = (e, tabIndex) => changeTab(tabIndex)
    return (
        <SimpleModal 
            isOpen={isOpen} 
            handleClose = {handleClose}
            title={title || "Hi There!"}
            className={classes.loginModal}
        >
            <Paper square>
                <Tabs
                    value={visibleTabIndex}
                    indicatorColor="primary"
                    textColor="primary"
                    onChange={handleTabSwitch}
                    centered
                    aria-label="tabs to create the story"
                >
                    <Tab label="Sign in" />
                    <Tab label="Sign up" />
                    <Tab label="Recovery" />
                </Tabs>
            </Paper>
            
            {/* Sign In Section */}
            <Box className={visibleTabIndex !== 0 ? 'hide' : ''}>
                <TextField label="Email" variant="outlined" size="small" fullWidth className="margin-top-25" />
                <TextField label="Password" type="password" variant="outlined" size="small" fullWidth className="margin-top-25" />
                <Button 
                    variant="outlined" 
                    color="primary"
                    className="margin-top-25"
                    fullWidth
                    // onClick={() => setIsImportModalOpen(true)}
                >
                    Sign in
                </Button>
            </Box>

            {/* Sign Up Section */}
            <Box className={visibleTabIndex !== 1 ? 'hide' : ''}>
                Signup
            </Box>

            {/* Password Recovery Section */}
            <Box className={visibleTabIndex !== 2 ? 'hide' : ''}>
                Recovery
            </Box>
            

            <Box align="center" my={1}>
                <Typography variant="h6" gutterBottom>
                    <b>OR</b>
                </Typography>
            </Box>

            
        </SimpleModal>
    )
}

const mapStateToProps = state => ({
    isOpen: state.ui.isLoginModalOpen,
    visibleTabIndex: state.ui.loginActiveTab
})

const mapDispatchToProps = dispatch => ({
    handleClose: () => dispatch({type: UI_LOGIN_MODAL_CLOSE}),
    changeTab: tabIndex => dispatch({type: UI_LOGIN_SET_ACT_TAB, payload: {tabIndex}})
})

export default connect(mapStateToProps, mapDispatchToProps)(LoginModal)